}
@media only screen and (max-width: 1500px) {
	.show-message #main {
		margin-right: 0;
	}
	.show-message #message {
		left: 50%;
		width: 50%;
	}
}
@media only screen and (max-width: 1024px) {
	.show-sidebar #sidebar {
		-webkit-transform: none;
		transform: none;
	}
	.show-sidebar #main {
		-webkit-transform: translateX(250px);
