		display: none;
	}
}
.nano {
	position: relative;
	width: 100%;
	height: 100%;
	overflow: hidden;
}
.nano > .nano-content {
	position: absolute;
	overflow: scroll;
	overflow-x: hidden;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
}
.nano > .nano-content:focus {
	outline: none;
}
.nano > .nano-content::-webkit-scrollbar {
	visibility: hidden;
}
.has-scrollbar > .nano-content::-webkit-scrollbar {
	visibility: visible;
}
.nano > .nano-pane {
	background: rgba(152, 166, 173, 0.1);
	position: absolute;
	width: 7px;
	right: 8px;
	top: 8px;
	bottom: 8px;
	visibility: hidden \9;
	opacity: 0.01;
	border-radius: 5px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
}
.nano > .nano-pane > .nano-slider {
	background: rgba(152, 166, 173, 0.35);
	position: relative;
	border-radius: 3px;
	-webkit-transition: 0.3s;
	transition: 0.3s;
	-webkit-transition-property: background;
	transition-property: background;
}
.nano > .nano-pane:hover > .nano-slider,
.nano > .nano-pane.active > .nano-slider {
	background: #a6a6a6;
}
.nano:hover > .nano-pane,
.nano-pane.active,
.nano-pane.flashed {
	visibility: visible \9;
	opacity: 0.99;
}
