	box-shadow: 0 0px 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
	display: block;
	float: left;
	height: 100%;
	position: fixed;
	transition: all 200ms ease-out;
	width: 240px;
}
.right-bar {
	background: #ffffff !important;
	z-index: 999 !important;
}
